export default ".container {\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n}\n\n.wrapper {\n  margin-right: auto;\n  margin-left: auto;\n  display: grid;\n  height: 100vh;\n  width: 95vw;\n  grid-template-rows: 20vh 2fr;\n}\n\n@media (min-width: 700px) {\n  .wrapper {\n    display: grid;\n    grid-template-columns: 160px 2fr;\n    grid-template-rows: 1fr 1fr;\n    width: 1000px;\n    height: 95vh;\n  }\n}\n\n.wrapper .slider {\n  grid-column: 1/3;\n  grid-row: 1/2;\n  height: 20vh;\n  display: block;\n  border-bottom-left-radius: 0;\n  border-top-left-radius: 0;\n  position: relative;\n  z-index: 1;\n  background-color: #48a5ea;\n}\n\n@media screen and (min-width: 700px) {\n  .wrapper .slider {\n    grid-column: 1/2;\n    grid-row: 1/3;\n    height: 100%;\n    border-top-left-radius: 10px;\n    border-bottom-left-radius: 10px;\n    display: block;\n    position: relative;\n    z-index: 1;\n  }\n}\n\n.wrapper .slider-app {\n  background-color: #48a5ea;\n  width: 100%;\n  height: 100%;\n  display: block;\n  border-bottom-left-radius: 20px;\n  border-top-left-radius: 20px;\n  position: relative;\n  z-index: 2;\n}\n\n.wrapper .form {\n  grid-column: 1/3;\n  grid-row: 2/3;\n  background-color: #fff;\n  position: relative;\n  display: block;\n  box-sizing: border-box;\n}\n\n@media screen and (min-width: 700px) {\n  .wrapper .form {\n    grid-column: 2/3;\n    grid-row: 1/3;\n    border-top-right-radius: 10px;\n    border-bottom-right-radius: 10px;\n  }\n}\n\n.wrapper .modal {\n  display: block;\n  opacity: 1;\n  position: fixed;\n  z-index: 99;\n  left: 0;\n  top: 0;\n  height: 100%;\n  width: 100%;\n  overflow: auto;\n  background-color: rgba(0, 0, 0, 0.5);\n}\n\n.wrapper .modal__content {\n  background-color: #fff;\n  margin: 20% auto;\n  width: 50vw;\n  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.2);\n  display: flex;\n  flex-direction: column;\n  border-radiu